.chart {
    display: table;
    width: 100%;
    color: #999
}

.chart canvas {
    display: block;
    position: absolute;
    width: 100%;
    height: 0;
}

.chart--canvasArea {
    display: table-cell;
    width: 95%;
}

.chart--studiesWrapper {
    display: table-row;
}

.chart--studiesInner {
    display: table-cell;
    column-span: all;
    /* W3C */
    -webkit-column-span: all;
    /* Safari & Chrome */
    -moz-column-span: all;
    /* Firefox */
    -ms-column-span: all;
    /* Internet Explorer */
    -o-column-span: all;
    /* Opera */
}

.chart--studies {
    display: table-row-group;
    width: 100%;
}

.chart--graph, .chart--axisWrapper-x, .chart--map {
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

.chart--map {
    display: table-row;
}

.chart--axisWrapper-x, .chart--graph {
    display: table-row;
}

.chart--graph-resizable {
    display: table-row;
    height: 100%;
}

.chart--resizeHandle {
    height: 3px;
    cursor: s-resize;
}

.chart--axisWrapper-y {
    display: table-cell;
    width: 5%;
    max-width: 60px;
    vertical-align: top;
}

.chart--axis-y {
    position: relative;
    width: 100%;
    height: 100%;
    font-size: 9pt;
    font-family: sans-serif;
}

.chart--axisWrapper-x {
    height: 20px;
}

.chart--axis-x {
    position: relative;
    display: table-cell;
    padding: 3px 0 0;
    border: 1px solid;
    font-family: sans-serif;
}

.chart--axis-x>.current, .chart--axis-y>.current, .chart--axis-y>.last {
    border: 1px solid;
    border-radius: 5px;
}

.chart--axis-x>div, .chart--axis-y>div {
    position: absolute;
    font-size: 9px;
}

.chart--axis.chart--axis-y.chart--axisExpander>div {
    position: relative;
    visibility: hidden;
    line-height: 0;
    margin-top: 0;
    margin-bottom: 0;
    padding-top: 0;
    padding-bottom: 0;
}

.chart--axis-y>div {
    margin: -7px 5px 0;
    padding: 1px 3px;
}

.chart--canvasWrapper {
    position: relative;
    width: 100%;
    height: 100%;
}

.crossTool--line {
    position: absolute;
}

.crossTool--line-horizontal {
    left: 0;
    width: 100%;
    border-top: 1px dotted;
    opacity: 0.5;
}

.crossTool--line-vertical {
    top: 0;
    height: 100%;
    border-left: 1px dotted;
    opacity: 0.5;
}

.crossTool--label span {
    display: inline-block;
    margin: 5px;
    padding: 3px 10px;
    border: 1px solid;
    border-radius: 15px;
    white-space: nowrap;
    font-size: 10px;
    font-family: sans-serif;
}

.crossTool--label-volume {
    position: absolute;
    bottom: 5px;
    left: 0;
}

.crossTool--stickPoint {
    position: absolute;
    display: none;
    margin-top: -3px;
    margin-left: -3px;
    width: 7px;
    height: 7px;
}

.chart--legend {
    position: absolute;
    top: 0;
    z-index: 2;
    display: inline-block;
    margin-left: 10px;
    cursor: pointer;
}

.chart--legend>p {
    overflow: hidden;
    width: 300px;
    text-overflow: ellipsis;
    white-space: nowrap;
    font-weight: normal;
    font-size: 14px;
}

.chart--legend>p:hover {
    width: auto;
}

.chartStudy--infoBar {
    font-size: 14px;
}

.chart--highLowIndicator {
    position: absolute;
    padding: 4px;
    border-radius: 4px;
    opacity: 0.5;
}

.chart--highLowIndicator-low {
    margin-top: -2em;
}

.chartStudy-overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.chartStudy--marker {
    display: inline-block;
    margin: 3px 3px 1px 7px;
    width: 7px;
    height: 7px;
}

.chartMap {
    position: relative;
    display: table-cell;
}

.chart--map {
    height: 35px;
}

.chartMap--canvasWrapper {
    position: relative;
    margin: 0 18px;
    height: 100%;
}

.chartMap--labels {
    position: absolute;
    top: 0;
    width: 100%;
    height: 100%;
}

.chartMap--labels>div {
    position: absolute;
    height: 100%;
    border-left: 1px solid;
}

.chartMap--controls {
    position: absolute;
    top: 0;
    display: table;
    width: 100%;
    height: 100%;
}

.chartMap--button {
    position: absolute;
    top: 0;
    width: 18px;
    height: 36px;
    border: none;
    font-weight: bold;
}

.chartMap--button-toLeft {
    left: 0;
}

.chartMap--button-toRight {
    right: 0;
}

.chartMap--area {
    display: table-cell;
}

.chartMap--area-hidden {
    width: 10px;
    height: 100%;
    border: 1px solid;
    vertical-align: middle;
}

.chartMap--area:first-child {
    text-align: right;
}

.chartMap--knob {
    display: inline-block;
    width: 5px;
    height: 33px;
    border: 1px solid;
    background: white;
    cursor: e-resize;
}

.chartMap--knob-toLeft {
    right: 0;
    margin-right: -4px;
    border-radius: 5px 0 0 5px;
}

.chartMap--knob-toRight {
    left: 0;
    margin-left: -4px;
    border-radius: 0 5px 5px 0;
}

.chartMap--window {
    display: inline-block;
    width: 100%;
    height: 100%;
}

.chartMap--container {
    position: relative;
}

.chartMap--area {
    border: none;
    height: 35px;
}
